export default function MyAircraft() {
    const {dispatch,state} = useWeb3();
    const { Mock29Contract,Mock21Contract,Mock24Contract,Mock27Contract,Account,MarketContract,showpage} = state;

    const [activeItem, setActiveItem] = useState(null);
    const [page,setPage] = useState(0);
    const [pagesize,setPagesize] = useState(1);
    const [pageTotal, setPageTotal] = useState(0);
    const [tips, setTips] = useState(null);
    const [arrlist, setArrlist] = useState([]);
    const [dList, setDList] = useState([]);
    const [modalType, setModalType] = useState('');

    if(window.ethereum){
        window.ethereum.on('accountsChanged', function (arr) {
            dispatch({type: 'SET_ALLACCOUNT',payload:arr[0]});
        });

    }else{
        console.log("===window.ethereum not found=====")
    }

    // useEffect(() => {
    //     dispatch({ type: 'SHOW_PAGE', payload: {page:0,pagesize:2} });
    // },[]);

    useEffect(() => {
        // dispatch({ type: 'SHOW_PAGE', payload: true });
        if(Mock29Contract == null || Account == null || MarketContract == null ) return;
        init();

    },[Mock29Contract,Account,MarketContract]);

    useEffect(() => {

        if(document.body.clientWidth > 1000){
            setPagesize(6)
        }else{
            setPagesize(2)
        }
    },[]);
    useEffect(() => {
        if(showpage == null ) return;
        if(showpage.page === page || showpage.page  <0) return;
        setPage(parseInt(showpage.page))
    },[showpage]);
    useEffect(() => {
        // dispatch({ type: 'SHOW_PAGE', payload: true });
        if(!arrlist.length) return;

        dispatch({ type: 'SHOW_PAGE', payload: {page,pagesize,total:arrlist.length} });
        getDisplay()

    },[arrlist,page,pageTotal]);

    const getDisplay = () =>{
        if(arrlist.length < pagesize){
            setDList(arrlist)
        }else{
           let pageTotal =  arrlist.length / pagesize;
           setPageTotal(pageTotal)
            if(page > pageTotal || page <0) {
                return;
            }else{
                let startIndex = page * pagesize;
                let endIndex = (page + 1) * pagesize
                let currentArr = arrlist.slice(startIndex,endIndex);
                setDList(currentArr);
            }
        }

    }

    const init = async()=>{
        let arr = []
        let arr21 = await TypeDisplay(21);
        let arr24 = await TypeDisplay(24);
        let arr27 = await TypeDisplay(27);
        let arr29 = await TypeDisplay(29);
        let arrobjlist = arr.concat(arr21,arr24,arr27,arr29)

        // let arr21 = await getList(Mock21Contract,21);
        //
        // if(arr21){
        //     [...Array(arr21.length)].map(item=> arr.push(myX21))
        //     // arr21.map(async (item)=>{
        //     //     let tokenURI = await mockx.tokenURI(contract,item);
        //     //     console.error("=====tokenURI====",tokenURI)
        //
        //     // })
        // }

        dispatch({type: 'SET_LOADING',payload:null});
        let arrObj = FormatJson(arrobjlist);

        setArrlist(arrObj)
    }

    const getOrder = async () => {
        let len = await marketCt.numberOf(MarketContract,Account);
        let myOrderlistArr = await marketCt.getUserOrderIds(MarketContract,Account,0,len.toString());
        let orderArr=[];
        myOrderlistArr.map(async (item)=>{
            let obj =  await marketCt.orderList(MarketContract,item.toString());
            let aftrobj = {
                obj,
                orderid:item.toString()
            }

            orderArr.push(aftrobj);
        })
        // setMyOrderList(orderArr);
        return orderArr;
    }

    const getList = async (contract) =>{
        dispatch({type: 'SET_LOADING',payload:true});

        let totalfighter = await mockx.getBalance(contract,Account);
        const total = totalfighter && BigNumber.from(totalfighter).toNumber();

        if (!total) return
        const promiseArr = []
        for (let index = 0; index < total; index++) {
            const rs = mockx.tokenOfOwnerByIndex(contract,Account,index);
            promiseArr.push(rs)
        }
        const fighterIds = await Promise.all(promiseArr)
        const fighterArr = fighterIds.toString().split(",");
        // dispatch({type: 'SET_LOADING',payload:null});
        return fighterArr
    }

    const TypeDisplay = async(type) =>{

        let orderArr = await getOrder();
        let contract;
        let displayObj;
        // let myArr=[];
        let addr;

        switch (type) {
            case 21:
                addr = window.GeneralConfig.mockX21;
                contract = Mock21Contract;
                displayObj = myX21;
                break;
            case 24:
                addr = window.GeneralConfig.mockX24;
                contract = Mock24Contract;
                displayObj = myX24;
                break;
            case 27:
                addr = window.GeneralConfig.mockX27;
                contract = Mock27Contract;
                displayObj = myX27;
                break;
            case 29:
                addr = window.GeneralConfig.mockX29;
                contract = Mock29Contract;
                displayObj = myX29;
                break;
            default:break;
        }
        let arr = await getList(contract,type);

        let allOrderList = [];
        if(arr){
            arr.map( itemObj =>{
                allOrderList.push({
                    nft:addr,
                    tokenId: itemObj
                })

            })

        }
        if(orderArr.length){
            orderArr.map((item,index)=>{
                if(!item.obj.currency || item.obj.nft !== addr || item.obj.status !== 1 )return;
                allOrderList.push({
                     seller:item.obj.seller,
                    nft:item.obj.nft,
                    currency:item.obj.currency,
                    tokenId:item.obj.tokenId.toString(),
                    price:item.obj.price.toString(),
                    timestamp:item.obj.timestamp.toString(),
                    status:item.obj.status,
                    orderid:item.orderid
                })
            })

        }

        if(allOrderList){
            allOrderList.map(item=>{
                item.info = displayObj;
                item.type = type;
            })
        }

        return allOrderList;
    }

    const FormatJson = (arrObj) =>{
        if(!arrObj)return;
        arrObj.map(item=>{
            let attr={}
            item.info.attributes.map(p=> attr[p.trait_type]=p.value)
            item.attr = attr;
            return item;
        })
        return arrObj

    }
    const SaleAircraft =  (item,typeStr) =>{
        dispatch({type: 'SHOW_MODAL',payload: true});
        setActiveItem(item)
        setTips("Confirm the sale of your aircraft .")
        setModalType(typeStr)
    }
    const CancelAircraft = (item,typeStr) =>{
        dispatch({type: 'SHOW_MODAL',payload: true});
        setActiveItem(item)
        setTips("Are you sure you want to cancel the current aircraft sale ?")
        setModalType(typeStr)
    }



    return <Content>
        <ConfirmModal item={activeItem} tips={tips} buttonTips={modalType} />
        <Ul>
            {
                dList.map((item,index)=>(
                    <li key={`my_${index}`}>
                        <Lilft>
                            <Imgs src={item.info.image} alt=""/>
                            <ImgsBg src={aircraftLft} alt=""/>
                            {/*<RhtA onClick={()=>SaleAircraft(item)} disabled={true}>Sale</RhtA>*/}
                            {
                                !item.status &&<RhtA onClick={()=>SaleAircraft(item,'upload')}>Sale</RhtA>
                            }
                            {
                                item.status === 1 &&<RhtBtn onClick={()=>CancelAircraft(item,'cancel')}>Cancel</RhtBtn>
                            }
                        </Lilft>
                        <Lirht>
                            <Titles>{item.info.name}</Titles>

                            <div>
                                <ImgRht src={listli} alt="" />
                                <ImgRht src={Line2} alt="" />
                            </div>
                            <SubTit>Energy recovery speed</SubTit>
                            <Speed><span>{item.attr.Recover_Speed}</span></Speed>
                            <SubTit>Number of equipment</SubTit>
                            <Speed><span>{item.attr.Slots}</span></Speed>

                            <LastRow>
                                {
                                    item.price &&<LftPrice>
                                        <img src={PriceImg} alt=""/>

                                        <Price>{ethers.utils.formatEther(item.price.toString())} <span>{item.symbol}</span></Price>
                                    </LftPrice>
                                }

                            </LastRow>
                        </Lirht>
                    </li>
                ))
            }
        </Ul>
        </Content>
}
